import React, { useEffect, useState } from "react";
import errorImg from "../../../images/error_img.svg";
import { Button } from "@material-ui/core";
import { useLocation } from "react-router-dom";
import { Link } from "react-router-dom";

const ErrorScreen = props => {
  const location = useLocation();

  const [error, setError] = useState({});

  useEffect(() => {
    console.log(props);
    console.log(location);

    var search = location.search.substring(1);
    var temp = JSON.parse(
      '{"' +
        decodeURI(search)
          .replace(/"/g, '\\"')
          .replace(/&/g, '","')
          .replace(/=/g, '":"') +
        '"}'
    );

    setError(temp);
    console.log(temp);
  }, []);

  return (
    <div
      style={{
        height: "88vh",
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        justifyContent: "center"
      }}
    >
      <img src={errorImg} style={{ height: 250, marginBottom: 32 }} />
      <h1>{error ? error.title : props.title}</h1>
      <span>{error.msg ? error.msg : ""}</span>

      <Link style={{ textDecoration: "none" }} to="/login">
        <Button style={{ marginTop: 25 }} variant="contained" color="primary">
          Got to login
        </Button>
      </Link>
    </div>
  );
};

export default ErrorScreen;
