// https://github.com/dockwa/simple-react-validator/issues/97
import React, { useEffect, useState, useRef } from "react";
import { useLocation, useHistory } from "react-router-dom";
import Service from "./../utils/service";
import SimpleReactValidator from "simple-react-validator";
import {
  TextField,
  makeStyles,
  Card,
  Button,
  Grid,
  FormControl,
  FormControlLabel,
  RadioGroup,
  Radio,
  FormLabel,
  Divider,
  Select,
  MenuItem,
  InputLabel,
  Chip
} from "@material-ui/core";
import Constants from "./../utils/constants";
import MButton from "./../components/mButton";

const useStyles = makeStyles(theme => ({
  container: {
    display: "flex",
    flexWrap: "wrap",
    paddingLeft: "10px",
    paddingRight: "30px"
    // justifyContent: "center"
    // flexDirection: "column",
    // alignItems: "center"
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    justifyContent: "center",
    textAlign: "center",
    width: "100%"
  },
  formControl: {
    marginTop: 25,
    width: "100%",
    marginLeft: "6px"
  },
  formTitle: {
    paddingLeft: 18,
    marginTop: "10px"
  },
  error: {
    color: "red"
  }
}));

const AddUser = props => {
  let location = useLocation();
  let history = useHistory();
  const [, forceUpdate] = useState();
  const simpleValidator = useRef(new SimpleReactValidator());

  const classes = useStyles();
  const [status, setStatus] = useState(Constants.SUCCESS);
  const [rolesList, setRolesList] = useState([]);
  const [selectedTags, setSelectedTags] = useState([]);

  var projectListString = sessionStorage.getItem("projectsList");
  var projectsList = [];
  try {
    projectsList = JSON.parse(projectListString);
  } catch {
    projectsList = [];
  }

  var defaultValues = {
    username: "",
    firstName: "",
    lastName: "",
    email: "",
    companyName: "",
    position: "",
    phoneNumber: "",
    country: "",
    language: "en-US",
    tags: [],
    projectId: "",
    roleId: ""
  };

  const [userDetails, setUserDetails] = React.useState(defaultValues);

  useEffect(() => {
    if (props.type === "update") {
      setStatus(Constants.LOADING);
      Service.axiosApi
        .post(Service.apis.getUserByID, {
          username: location.state
        })
        .then(res => {
          if (res.data !== null) {
            setUserDetails(res.data);
            setSelectedTags(res.data.tags);
            fetchRoles(res.data.projectId);
          }

          setStatus(Constants.SUCCESS);
        })
        .catch(e => {
          console.log(e);
          setStatus(Constants.FAIL);
        });
    }
  }, []);

  const handleChange = event => {
    var temp = JSON.parse(JSON.stringify(userDetails));
    temp[event.target.name] = event.target.value;
    setUserDetails(temp);
    console.log("test");

    // simpleValidator.current.showMessages();
  };

  const handleKeyPress = event => {
    if (event.key === "Enter") {
      var value = event.target.value;

      if (value.trim() !== "") {
        setSelectedTags([...selectedTags, value]);
        setUserDetails({ ...userDetails, tags: selectedTags });
        event.target.value = "";
      }
    }
  };

  const handleSelect = (e, key) => {
    var value = e.target.value;
    switch (key) {
      case "project":
        setUserDetails({ ...userDetails, projectId: value });
        fetchRoles(value);
        break;
      case "country":
        setUserDetails({ ...userDetails, country: value });
        break;
      case "role":
        setUserDetails({ ...userDetails, roleId: value });
        break;
    }
  };

  const onFormSubmit = data => {
    var temp = { ...userDetails };
    temp.tags = selectedTags;
    temp.username = userDetails.email;
    console.log(data);

    simpleValidator.current.showMessages();
    forceUpdate(1);

    if (simpleValidator.current.allValid()) {
      if (location.state) {
        updateUser(temp);
      } else {
        saveUser(temp);
      }
    }
  };

  const saveUser = userData => {
    console.log("Saving User");
    setStatus(Constants.LOADING);
    Service.axiosApi
      .post(Service.apis.createuser, userData)
      .then(res => {
        alert(res);
        setStatus(Constants.SUCCESS);
        history.push("/admin");
      })
      .catch(e => {
        console.log(e);
        setStatus(Constants.FAIL);
      });
  };

  const updateUser = userData => {
    setStatus(Constants.LOADING);

    Service.axiosApi
      .post(Service.apis.updateuser, userData)
      .then(res => {
        setStatus(Constants.SUCCESS);
        history.push("/admin");
      })
      .catch(e => {
        setStatus(Constants.FAIL);
        console.log(e);
      });
  };

  function fetchRoles(projectId) {
    Service.axiosApi
      .post(Service.apis.getRolesList, {
        projectId: projectId
      })
      .then(res => {
        setRolesList(res.data);
      })
      .catch(err => {
        console.log(err);
      });
  }

  return (
    <div>
      <Grid item md={4} xs={12} style={{ margin: "35px auto" }}>
        <Card>
          <h3 className={classes.formTitle}>
            {props.type === "add" ? "Create User" : "Update User"}
          </h3>
          <Divider />
          <form className={classes.container} noValidate autoComplete="off">
            <Grid container spacing={4}>
              <Grid item md={12} xs={12}>
                <TextField
                  required
                  id="standard-required"
                  label="First Name"
                  className={classes.textField}
                  margin="normal"
                  name="firstName"
                  onChange={handleChange}
                  value={userDetails.firstName}
                />

                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "firstName",
                    userDetails.firstName,
                    "required"
                  )}
                </span>

                <TextField
                  required
                  id="standard-required"
                  label="Last Name"
                  className={classes.textField}
                  margin="normal"
                  name="lastName"
                  onChange={handleChange}
                  value={userDetails.lastName}
                />
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "lastName",
                    userDetails.lastName,
                    "required"
                  )}
                </span>

                <TextField
                  required
                  id="standard-required"
                  label="Email"
                  className={classes.textField}
                  margin="normal"
                  name="email"
                  InputProps={{
                    readOnly: location.state ? true : false
                  }}
                  onChange={handleChange}
                  value={userDetails.email}
                />
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "email",
                    userDetails.email,
                    "required"
                  )}
                </span>

                <TextField
                  required
                  id="standard-required"
                  label="Phone Number"
                  className={classes.textField}
                  margin="normal"
                  name="phoneNumber"
                  onChange={handleChange}
                  value={userDetails.phoneNumber}
                />
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "phoneNumber",
                    userDetails.phoneNumber,
                    "required"
                  )}
                </span>

                <TextField
                  required
                  id="standard-required"
                  label="Company Name"
                  className={classes.textField}
                  margin="normal"
                  name="companyName"
                  onChange={handleChange}
                  value={userDetails.companyName}
                />
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "companyName",
                    userDetails.companyName,
                    "required"
                  )}
                </span>

                <TextField
                  required
                  id="standard-required"
                  label="Position"
                  className={classes.textField}
                  margin="normal"
                  name="position"
                  onChange={handleChange}
                  value={userDetails.position}
                />
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "position",
                    userDetails.position,
                    "required"
                  )}
                </span>

                <FormControl
                  className={(classes.menuDropdown, classes.formControl)}
                >
                  <InputLabel id="country">Country</InputLabel>
                  <Select
                    onChange={e => {}}
                    labelId="country"
                    id="country-select"
                    value="US"
                  >
                    <MenuItem value="US" name="US">
                      US
                    </MenuItem>
                  </Select>
                </FormControl>

                <FormControl
                  className={(classes.menuDropdown, classes.formControl)}
                >
                  <InputLabel id="language">language</InputLabel>
                  <Select
                    onChange={e => {}}
                    labelId="language"
                    id="language-select"
                    value="en-US"
                  >
                    <MenuItem value="en-US" name="en-US">
                      en-US
                    </MenuItem>
                  </Select>
                </FormControl>

                <FormControl
                  className={(classes.menuDropdown, classes.formControl)}
                >
                  <InputLabel id="projects">Select Project</InputLabel>
                  <Select
                    onChange={e => {
                      handleSelect(e, "project");
                    }}
                    labelId="projects"
                    id="language-select"
                    value={userDetails.projectId ? userDetails.projectId : " "}
                  >
                    {/* <MenuItem value="" name="select project">
                      Select Project
                    </MenuItem> */}
                    {projectsList.map((row, i) => {
                      return (
                        <MenuItem
                          value={row.projectId}
                          name={row.title}
                          key={row.title + i}
                        >
                          {row.title}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "projectId",
                    userDetails.projectId,
                    "required"
                  )}
                </span>

                <FormControl
                  className={(classes.menuDropdown, classes.formControl)}
                >
                  <InputLabel id="role">Select Role</InputLabel>
                  <Select
                    onChange={e => {
                      handleSelect(e, "role");
                    }}
                    labelId="role"
                    id="role-select"
                    value={userDetails.roleId ? userDetails.roleId : " "}
                  >
                    {/* <MenuItem value="" name="select project">
                      Select Project
                    </MenuItem> */}
                    {rolesList.map((row, i) => {
                      return (
                        <MenuItem
                          value={row.roleid}
                          name={row.title}
                          key={row.title + i}
                        >
                          {row.title}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
                <span className={classes.error}>
                  {simpleValidator.current.message(
                    "roleId",
                    userDetails.roleId,
                    "required"
                  )}
                </span>

                <FormControl
                  className={(classes.menuDropdown, classes.formControl)}
                >
                  <TextField
                    id="standard-basic"
                    label="tags"
                    onKeyPress={handleKeyPress}
                  />
                  <div style={{ marginTop: 5 }}>
                    {selectedTags.map((row, i) => {
                      return (
                        <Chip
                          style={{ margin: 5 }}
                          label={row}
                          onDelete={() => {
                            setSelectedTags(
                              selectedTags.filter(item => item !== row)
                            );
                          }}
                        />
                      );
                    })}
                  </div>
                </FormControl>
              </Grid>
            </Grid>
          </form>
          <div style={{ marginTop: 25 }}>
            <MButton
              variant="contained"
              color="primary"
              lcolor="white"
              className="btn btn-red text-uppercase login_button pull-right"
              style={{
                marginBottom: 30,
                marginTop: 15,
                float: "right",
                marginRight: "15px"
              }}
              onClick={e => onFormSubmit(e)}
              status={status}
            >
              {location.state ? "Update user" : "Create User"}
            </MButton>
            <Button
              variant="contained"
              style={{
                marginBottom: 30,
                marginTop: 15,
                float: "right",
                marginRight: "15px",
                padding: "8px 25px"
              }}
              onClick={e => history.push("/admin")}
            >
              Cancel
            </Button>
          </div>
        </Card>
      </Grid>
    </div>
  );
};

export default AddUser;
