import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Service from "./utils/service";

import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Switch,
  FormControlLabel,
  MenuItem,
  FormControl,
  Select,
  InputLabel,
  Button
} from "@material-ui/core";

import AccountCircle from "@material-ui/icons/AccountCircle";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import Menu from "@material-ui/core/Menu";
import Logo from "./../images/logo.jpg";
import { Route, Link, useLocation } from "react-router-dom";
import Constants, { reduxConstants } from "./utils/constants";
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  menuButton: {
    marginRight: theme.spacing(2)
  },
  userIcon: {
    margin: "0 0 0 auto"
  },
  menuDropdown: {
    margin: "0px 25px",
    display: "flex",
    minWidth: "240px",
    paddingBottom: "15px"
  },
  fullBtn: {
    padding: "18px"
  },
  btnActive: {
    borderBottom: "4px solid red",
    borderRadius: "unset",
    padding: "18px"
  },
  title: {}
}));

function MenuAppBar(props) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const [projectId, setProjectId] = React.useState("");
  const [userDetails, setUserDetails] = React.useState({});
  let location = useLocation();
  let history = useHistory();

  const handleChange = event => {
    setProjectId(event.target.value);
    sessionStorage.setItem(Constants.SELECTED_PROJECT, event.target.value);
    console.log(event.target);
  };

  useEffect(() => {
    setProjectId(props.projectId);
    setUserDetails(JSON.parse(sessionStorage.getItem(Constants.USER_DETAILS)));
  }, []);

  const handleMenu = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = key => {
    setAnchorEl(null);
    switch (key) {
      case "logout":
        var arr = sessionStorage.getItem("profile").split("/");
        Service.axiosApi
          .post(Service.apis.logout, { profileId: arr[arr.length - 1] })
          .then(res => {
            sessionStorage.clear();
            window.location = "login";
          })
          .catch(err => {
            alert("Somethign went wrong!..");
          });

        break;
    }
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="white">
        <Toolbar>
          <img src={Logo} style={{ height: "40px" }} />
          <Typography variant="h6" className={classes.title}>
            Lamdata
          </Typography>
          <FormControl className={(classes.formControl, classes.menuDropdown)}>
            <InputLabel id="project">Project</InputLabel>
            <Select
              onChange={e => {
                handleChange(e);
                props.onChange(e);
              }}
              labelId="project"
              id="demo-simple-select"
              value={projectId}
            >
              {props.projectsList.map((val, i) => {
                return (
                  <MenuItem
                    value={val.projectId}
                    name={val.title}
                    key={val.title + i}
                  >
                    {val.title}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
          <div>
            <Link style={{ textDecoration: "none" }} to="/">
              <Button
                className={
                  location.pathname === "/"
                    ? classes.btnActive
                    : classes.fullBtn
                }
              >
                Dashboards
              </Button>
            </Link>
            <Link style={{ textDecoration: "none" }} to="/reports">
              <Button
                className={
                  location.pathname === "/reports"
                    ? classes.btnActive
                    : classes.fullBtn
                }
              >
                Reports
              </Button>
            </Link>
            {sessionStorage.getItem(Constants.IS_ADMIN) == "true" ? (
              <Link style={{ textDecoration: "none" }} to="/admin">
                <Button
                  className={
                    location.pathname === "/admin"
                      ? classes.btnActive
                      : classes.fullBtn
                  }
                >
                  Admin
                </Button>
              </Link>
            ) : null}
          </div>

          <div className={classes.userIcon}>
            <IconButton
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleMenu}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>
            {/* <Button onClick={handleMenu} href="#text-buttons" color="primary">
              <AccountCircle style={{ marginRight: 10 }} />
              {sessionStorage.getItem("username")}
            </Button> */}

            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right"
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right"
              }}
              open={open}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>
                <b>Name : </b> {userDetails ? userDetails.name : ""}
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <b>Company : </b> {userDetails ? userDetails.company : ""}
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <b>Position : </b> {userDetails ? userDetails.position : ""}
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <b>Phone Number : </b>{" "}
                {userDetails ? userDetails.phoneNumber : ""}
              </MenuItem>
              {/* <MenuItem onClick={handleClose}>Profile</MenuItem> */}
              <MenuItem onClick={e => handleClose("logout")}>
                <ExitToAppIcon style={{ marginRight: 10 }} /> Logout
              </MenuItem>
            </Menu>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}

export default MenuAppBar;
