import Constants from "./constants";

export const doSomethingWithInput = theInput => {
  //Do something with the input
  return theInput;
};

export const validate = function(rules, key, value, setErrorObject) {
  var rule = rules[key];

  if (rule == undefined) return;

  setErrorObject(key, "");

  test: for (const [rkey, rvalue] of Object.entries(rule)) {
    var flag = false;
    switch (rkey) {
      case "min":
        if (value.length < rvalue) {
          setErrorObject(key, " length should be min " + rvalue);
          break test;
        }
        break;
      case "required":
        if (value === undefined || value === null || value === "") {
          setErrorObject(key, " is required");
          break test;
        }
        break;
      case "email":
        if (!validateEmail(value)) {
          setErrorObject(key, "Invalid email");
          break test;
        }
        break;
    }
  }

  return true;
};

function validateEmail(email) {
  var re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(email);
}

export const validateAll = function(rules, dataJson, setErrorObject, callBack) {
  for (const [key, value] of Object.entries(dataJson)) {
    validate(rules, key, value, setErrorObject);
  }
  callBack("test");
};

export const checkValidation = function(errorsJson) {
  var isValid = true;
  test: for (const [key, value] of Object.entries(errorsJson)) {
    if (isValid) {
      isValid = value.trim().length === 0;
    }
  }
  return isValid;
};

export const isValidToken = function(token) {
  token = sessionStorage.getItem(Constants.COOKIE);
  var bool = token != null && token != undefined && token.length > 0;
  return bool;
};

export const isAllfieldsEmpty = function(obj) {
  for (var key in obj) {
    if (obj[key] !== null && obj[key] != "") return false;
  }
  return true;
};
