import React, { useEffect, useState } from "react";
import IframeComponent from "../components/iframeComponent";
import Service from "../utils/service";
import Constants, { reduxConstants } from "./../utils/constants";
import { useLocation } from "react-router-dom";

import {
  FormControl,
  Select,
  MenuItem,
  InputLabel,
  makeStyles
} from "@material-ui/core";
import { connect } from "react-redux";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  menuDropdown: {
    margin: "0px 25px",
    display: "flex",
    minWidth: "240px",
    paddingBottom: "15px"
  }
}));

var _targetUrl = "";

function Dashboard(props) {
  const classes = useStyles();
  let _ssoProvider = "pgptest-lamdata.na.gooddata.com";

  const _encryptedClaims = sessionStorage.getItem(Constants.CLAIM_ID);
  const [bool, setBool] = useState(false);

  const [selectedDashboard, setSelectedDashboard] = useState("");
  const [dashboardList, setDashboardList] = useState([]);
  let location = useLocation();

  useEffect(() => {
    console.log("rpject Id:   " + props.projectId);
    console.log("Location state : ");

    console.log(location.state);

    Service.axiosApi
      .post(Service.apis.getDashboardList, {
        projectId: sessionStorage.getItem(Constants.SELECTED_PROJECT)
      })
      .then(res => {
        setDashboardList(res.data);
        setSelectedDashboard(res.data[0].link);

        var value = res.data[0].link;
        var prjurl = value
          .substring(0, value.indexOf("/obj"))
          .replace("/md", "/projects");
        _targetUrl =
          "/dashboard.html#project=" + prjurl + "&dashboard=" + value;

        console.log(_targetUrl);
        setTimeout(function() {
          setBool(true);
        }, 1000);
      })
      .catch(err => {});
  }, [props.projectId]);

  function handleChange(event) {
    setSelectedDashboard(event.target.value);

    var value = event.target.value;
    var prjurl = value
      .substring(0, value.indexOf("/obj"))
      .replace("/md", "/projects");
    _targetUrl = "/dashboard.html#project=" + prjurl + "&dashboard=" + value;
    console.log(_targetUrl);
  }

  return (
    <div>
      <div style={{ width: "300px", margin: "0 auto" }}>
        <FormControl className={(classes.formControl, classes.menuDropdown)}>
          <InputLabel id="project">Dashboard</InputLabel>
          <Select
            labelId="project"
            id="demo-simple-select"
            value={selectedDashboard}
            onChange={handleChange}
          >
            {dashboardList.map((val, i) => {
              return (
                <MenuItem value={val.link} name={val.title} key={i}>
                  {val.title}
                </MenuItem>
              );
            })}
          </Select>
        </FormControl>
      </div>

      <IframeComponent
        targetUrl={_targetUrl}
        ssoProvider={_ssoProvider}
        encryptedClaims={_encryptedClaims}
      />
    </div>
  );
}

export default Dashboard;
