import React from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";
import Login from "./app/screens/auth/login";
import MainScreen from "./app/screens/mainScreen";
import ErrorScreen from "./app/screens/auth/errorScreen";

function App() {
  return (
    <Switch>
      <Route exact path="/">
        <ErrorScreen />
      </Route>
      <Route exact path="/login">
        <Login />
      </Route>
      <Route path="/">
        <ErrorScreen />
      </Route>
    </Switch>
  );
}

export default App;
