import React, { useState, useEffect } from "react";
import MButton from "../../components/mButton";
import Constants from "../../utils/constants";
import "../../theme/css/bootstrap.min.css";
import "../../theme/css/custom.css";
import Logo from "../../../images/logo.jpg";
import "./login.css";
import Service from "../../utils/service";
import {
  validate,
  isAllfieldsEmpty,
  checkValidation
} from "../../utils/validations";
import Fingerprint2 from "fingerprintjs2";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { Button } from "@material-ui/core";

const useStyles = makeStyles(theme => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3)
  }
}));

const Login = props => {
  const classes = useStyles();

  var [userDetails, setUserDetails] = useState({ userName: "", password: "" });
  var [errors, setErrors] = useState({ userName: "", password: "" });
  const [status, setStatus] = useState("");

  const [modal, setModal] = React.useState({ open: false, msg: "" });

  var [browserUniqueKey, setBrowserUniqueKey] = useState();
  var [browserParams, setBrowserParams] = useState();

  var killsessionId = 0;

  useEffect(() => {
    setStatus(Constants.LOADING);
    if (window.requestIdleCallback) {
      requestIdleCallback(function(result) {
        Fingerprint2.get(function(components) {
          console.log(components);
          if (components) setBrowserParams(components); // an array of components: {key: ..., value: ...}
          Fingerprint2.getV18(function(result) {
            console.log("hashcode : " + result);
            if (result) setBrowserUniqueKey(result);
            setStatus(Constants.SUCCESS);
          });
        });
      });
    } else {
      setTimeout(function(result) {
        Fingerprint2.get(function(components) {
          browserParams = components; // an array of components: {key: ..., value: ...}
          Fingerprint2.getV18(function(result) {
            browserUniqueKey = result;
            setStatus(Constants.SUCCESS);
          });
        });
      }, 500);
    }
  }, []);

  var rules = {
    userName: {
      required: true,
      min: 3
    },
    password: {
      required: true,
      min: 3
    }
  };

  const handleOpen = () => {
    setModal({ ...modal, open: true });
  };

  const handleClose = () => {
    setModal({ ...modal, open: false });
  };

  var onTextChange = e => {
    setUserDetails({
      ...userDetails,
      [e.target.name]: e.target.value
    });

    validate(rules, e.target.name, e.target.value, setErrorObject);
  };

  var setErrorObject = function(key, value) {
    setErrors({
      ...errors,
      [key]: value
    });
  };

  var login = e => {
    var body = {
      username: userDetails.userName,
      password: userDetails.password,
      killsession: killsessionId,
      fingerPrint: {
        key: browserUniqueKey,
        params: browserParams
      }
    };

    let config = {
      headers: {
        "Content-Type": "application/json",
        "Accept-Language": "application/json"
      }
    };

    if (!isAllfieldsEmpty(userDetails) && checkValidation(errors)) {
      setStatus(Constants.LOADING);
      Service.axiosApi
        .post(Service.apis.login, body, config)
        .then(response => {
          console.log(response);

          sessionStorage.setItem(Constants.USERNAME, body.username);
          sessionStorage.setItem(Constants.IS_ADMIN, response.data.adflag);

          var userData = {
            name: response.data.name,
            company: response.data.company,
            position: response.data.position,
            phoneNumber: response.data.phoneNumber
          };
          sessionStorage.setItem(
            Constants.USER_DETAILS,
            JSON.stringify(userData)
          );
          // sessionStorage.setItem(Constants.NAME, JSON.stringify(userData));
          // sessionStorage.setItem(Constants.COMPANY, JSON.stringify(userData));
          // sessionStorage.setItem(Constants.POSITION, JSON.stringify(userData));
          // sessionStorage.setItem(Constants.PHONE_NUMBER, JSON.stringify(userData));

          sessionStorage.setItem(
            Constants.SELECTED_PROJECT,
            JSON.parse(response.data.projectList)[0].projectId
          );

          sessionStorage.setItem(
            Constants.PROJECTS_LIST,
            response.data.projectList
          );

          sessionStorage.setItem(Constants.CLAIM_ID, response.data.claim);
          sessionStorage.setItem(Constants.PROFILE_ID, response.data.profile);

          var GDCAuthSST_Token = response.headers["gd-cookie"]
            .split(",")[1]
            .split(";")[0];
          sessionStorage.setItem(Constants.COOKIE, GDCAuthSST_Token);
          window.location = "/";
          setStatus(Constants.SUCCESS);
        })
        .catch(err => {
          if (err.response.status === 401) {
            setModal({ open: true, msg: err.response.data.errorMessage });
          }

          setStatus(Constants.FAIL);
        });
    } else {
      alert("validation failed");
    }
  };

  return (
    <div>
      <style
        dangerouslySetInnerHTML={{
          __html: `body { background: #FAFAFA }`
        }}
      />
      <div style={{ height: 15, backgroundColor: "red" }}></div>
      <div id="login" className="loginPanel">
        <div
          className="row m-0"
          style={{
            maxWidth: "900px",
            border: "1px solid #c5c2c2",
            backgroundColor: "white"
          }}
        >
          <div
            className="col-md-5 login_col"
            style={{
              backgroundColor: "unset",
              alignItems: "center",
              display: "flex",
              justifyContent: "center"
            }}
          >
            <div
              className="col-md-12 "
              style={{ padding: "0px", borderRight: "2px solid #c5c2c2" }}
            >
              <img src={Logo} />
              <h3 style={{ textAlign: "center " }}>Login to Lamdata</h3>
              {/* <a href="#">Create your own account</a> */}
            </div>
          </div>

          <div className="col-md-7 login_col" style={{}}>
            <div className="row">
              <form id="login_form" className="col-md-12">
                <div className="form-group ">
                  <label htmlFor="exampleInputPassword1">Email</label>
                  <input
                    v-model="loginDetails.username"
                    id="signin_username"
                    name="userName"
                    type="text"
                    className="form-control "
                    placeholder="Email"
                    aria-label="Large"
                    aria-describedby="inputGroup-sizing-sm"
                    onChange={onTextChange}
                    required
                  />
                  <span>{errors.userName}</span>
                </div>
                <div className="form-group ">
                  <label htmlFor="exampleInputPassword1">Password</label>
                  <input
                    v-model="loginDetails.password"
                    id="signin_password"
                    type="password"
                    className="form-control "
                    placeholder="Password"
                    aria-label="Large"
                    name="password"
                    aria-describedby="inputGroup-sizing-sm"
                    onChange={onTextChange}
                    required
                  />
                  <span>{errors.password}</span>
                </div>
              </form>
              <div
                className="mt-2 col-md-12"
                style={{
                  display: "inline-flex",
                  justifyContent: "space-between",
                  alignItems: "center"
                }}
              >
                <span
                  id="forget_password"
                  className="red"
                  style={{ cursor: "pointer" }}
                >
                  Forgot Password ?
                </span>
                {/* <button
                  id="login_button"
                  className="btn btn-red text-uppercase login_button pull-right"
                  type="submit"
                  onClick={login}
                  loading={status}
                >
                  LOG IN
                </button> */}
                <MButton
                  id="login_button"
                  className="btn btn-red text-uppercase login_button pull-right"
                  type="submit"
                  onClick={login}
                  status={status}
                >
                  Login
                </MButton>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={modal.open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500
        }}
      >
        <Fade in={modal.open}>
          <div className={classes.paper}>
            <h2 id="transition-modal-title">Warning</h2>
            <p id="transition-modal-description">
              {" "}
              <b>{modal.msg}</b>
            </p>

            <p>Do you want to kill previous session and have a fresh login</p>
            <Button
              style={{ marginTop: 15 }}
              variant="contained"
              color="primary"
              onClick={() => {
                killsessionId = 1;
                login();
              }}
            >
              Okay
            </Button>
          </div>
        </Fade>
      </Modal>
    </div>
  );
};

export default Login;
