import React, { useEffect, useState } from "react";
import IframeComponent from "../components/iframeComponent";
import Service from "../utils/service";
import Constants, { reduxConstants } from "../utils/constants";
import { useLocation } from "react-router-dom";

import {
  FormControl,
  Select,
  MenuItem,
  InputLabel,
  makeStyles
} from "@material-ui/core";
import { connect } from "react-redux";
import LoadingComponent from "../components/loadingComponent";
import LoadingPanel from "../components/loadingPanel";

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1
  },
  menuDropdown: {
    margin: "0px 25px",
    display: "flex",
    minWidth: "240px",
    paddingBottom: "15px"
  }
}));

var _targetUrl = "";

function ReportView(props) {
  let _ssoProvider = "pgptest-lamdata.na.gooddata.com";
  const _encryptedClaims = sessionStorage.getItem(Constants.CLAIM_ID);
  const [targetLink, setTargetLink] = useState("");
  const [status, setStatus] = useState(Constants.LOADING);
  let location = useLocation();

  useEffect(() => {
    if (location.state != undefined) {
      setTargetLink(location.state[Constants.IFRAME_URL]);
      setStatus(Constants.SUCCESS);
    } else {
      setStatus(Constants.FAIL);
    }
  }, [props.projectId]);

  return (
    <LoadingPanel status={status}>
      <div>
        <IframeComponent
          targetUrl={targetLink}
          ssoProvider={_ssoProvider}
          encryptedClaims={_encryptedClaims}
        />
      </div>
    </LoadingPanel>
  );
}

export default ReportView;
