import React from "react";
import { LoadingIcon6 } from "./loadingIcons";

function MButton(props) {
  if (props.status === "loading") {
    return (
      <button className="btn btn-red" style={{ ...props.style, minWidth: 100 }}>
        <LoadingIcon6 color={props.lcolor ? props.lcolor : "white"} />
      </button>
    );
  }
  return (
    <button className="btn" {...props}>
      {props.children}
    </button>
  );
}

export default MButton;
