import React from "react";
import axios from "axios";
import Constants from "./constants";
import { useHistory } from "react-router-dom";
import ErrorScreen from "../screens/auth/errorScreen";

var domain = "http://192.241.154.115:8000";
var BASE_URL = "/api";
var AUTH_URL = "/oauth";

var apis = {
  login: "/login",
  getUsers: "/getusers",
  getRolesList: "/getRolesList",
  getDashboardList: "/getDashboards",
  getReports: "/getReports",

  createuser: "/createuser",
  updateuser: "/updateuser",
  getUserByID: "/getGDuser",
  deleteuser: "/deleteuser",
  logout: "/logout"
};

var axiosApi = axios.create({
  baseURL: domain
});

axiosApi.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    if (error.response.status === 401) {
      if (window.location.pathname != "/login") {
        window.location =
          "error?msg=" +
          encodeURIComponent(error.response.data.errorMessage) +
          "&title=" +
          encodeURIComponent(error.response.data.errortype);
      }
      return Promise.reject(error);
    } else {
      return Promise.reject(error);
    }
  }
);

axiosApi.interceptors.request.use(config => {
  let token = sessionStorage.getItem(Constants.COOKIE);
  if (isValidToken(token)) {
    config.headers["token"] = token;
  } else {
    if (window.location.pathname != "/login") window.location = "login";
  }
  return config;
});

export const saveToken = function(str) {
  sessionStorage.setItem("token", str);
};

export const deleteToken = function() {
  sessionStorage.removeItem("token");
};

export const isValidToken = function(token) {
  token = sessionStorage.getItem(Constants.COOKIE);
  var bool = token != null && token != undefined && token.length > 0;
  return bool;
};

var Service = {
  axiosApi: axiosApi,
  apis: apis
};

export default Service;
